<template>
    <div>
        <u-button type="primary" @on-click="info">Display info prompt</u-button>
        <u-button @on-click="success">Display success prompt</u-button>
        <u-button @on-click="warning">Display warning prompt</u-button>
        <u-button @on-click="error">Display error prompt</u-button>
        <u-button @on-click="loading">Display loading...</u-button>
        <u-button @on-click="closable">Display a closable message</u-button>
    </div>
</template>
<script>
    export default {
        methods: {
            info () {
                this.$Message.info({
                    content: '这是一条普通的提示',
                    duration: 1000
                });
            },
            success () {
                this.$Message.success('This is a success tip');
            },
            warning () {
                this.$Message.warning('This is a warning tip');
            },
            error () {
                this.$Message.error('This is an error tip');
            },
            loading () {
                const msg = this.$Message.loading({
                    content: 'Loading...',
                    duration: 0
                });
                setTimeout(msg, 3000);
            },
            closable () {
                this.$Message.info({
                    content: 'Tips for manual closing',
                    duration: 1000,
                    closable: true
                });
            }
        }
    };
</script>



